*
{
  margin:0;
  padding:0;
  font-family: "Verdana", cursive, sans-serif;
  color: black;
}
body{
  background-image: url('http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png');
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width:100%;
}

.header
{
  color: #f71aa2;
  text-align: center;
}
.app {
  background: linear-gradient(5deg, #ff961d 0%, #f71aa2 100%);
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}
.question-section
{
  color:royalblue;
  font-size: 30px;
  position: relative;
  width: 100%;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}
.answer-section{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient( #ff961d 0%, #f71aa2 100%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
}
button:hover{
  opacity: 0.8;
}